.SearchForm-container {
    display: flex;
    justify-content: center;
    margin-bottom: 20px;
    background-color: #0c0c0c67;
    box-shadow: 0px 11px 13px 0px rgba(0, 0, 0, 0.2),
    0px 11px 11px 0px rgba(0, 0, 0, 0.14), 0px 12px 11px -1px rgba(0, 0, 0, 0.12);
}

.SearchForm {
    display: flex;
    gap:2px;
    /* align-items: center; */
    width: 100%;
    max-width: 600px;
    background-color: #0e0d0d6c;
    border-radius: 3px;
    overflow: hidden;
  }
  
  .SearchForm-button {
    display: inline-block;
    width: 48px;
    height: 48px;
    border: 0;
    background-image: url('https://image.flaticon.com/icons/svg/149/149852.svg');
    background-size: 40%;
    background-repeat: no-repeat;
    background-position: center;
    opacity: 0.6;
    transition: opacity 250ms cubic-bezier(0.4, 0, 0.2, 1);
    cursor: pointer;
    outline: none;
  }
  
  .SearchForm-button:hover {
    opacity: 1;
  }
  
  .SearchForm-button-label {
    width: 20px;
    height: 20px;
    /* position: absolute;
    padding: 0;
    overflow: hidden;
    clip: rect(0, 0, 0, 0);
    white-space: nowrap;
    clip-path: inset(50%);
    border: 0; */
  }
  
  .SearchForm-input {
    display: inline-block;
    width: 100%;
    font: inherit;
    font-size: 20px;
    border: none;
    outline: none;
    padding-left: 4px;
    padding-right: 4px;
  }
  
  .SearchForm-input::placeholder {
    font: inherit;
    font-size: 18px;
  }